.input-float{
	max-width: 3.5em;
}

.active{
	z-index: 100;
	display: block;
}

.inactive{
	z-index: -1;
	width: 0;
	height: 0;
	visibility: hidden;
	position: absolute;
	display: block;
}

.flex-grow{
	flex-grow: 100;
}

.range{
	margin: 0 1em !important;
}

.color-box{
	width: 3.5em;

}

.flexbox{
	display: flex !important;
}

.accordian-contents{
	padding: 0.5em 1em;
}

.accordian .list-item .center{
	background: none;
}


.accordian .list-item .right{
	background: none;
}

.accordian{
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: bottom;
    background-image: linear-gradient(0deg, #e4e4e4, #efefef 100%);
}

#splash-card{
	max-width: 90%;
	padding: 30px;
	margin-top: -20%;
}

.center-flex {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100%;
}

#go-btn{
	margin-top: 10px;
	width: 100%;
	text-align: center;
	height: 40px;
}

.btn-warning{
	background-color: #f59e00 !important;	
	color: white !important;
}

.progress-circular__primary{
	stroke: #ffffff !important;
}


.full-width{
	width: 100%;
}

.error input{
	border-bottom: 1px solid #b91616;
}

.error{
	color : #b91616;
}

.half-width{
	width: 50%;
}

.flexbox-centered{
	display: flex !important;
    align-items: center;
    justify-content: center;
}

.btn-default{
	background-color: #0076ff !important;
	color: white !important;
}

.btn-bottom-toolbar{
	margin: 0px !important;
	text-align: center;
	padding: 0px !important;
	border: none !important;
}

.btn-bottom-toolbar ons-button{
	border-radius: 0px !important;
	border: none !important;
	font-size: 12px;
	text-transform: uppercase;
	height: 52px !important;
	line-height: 52px !important;
	padding: 0px !important;

}

.btn-action{
	width: 100%;
	max-width: 400px;
	text-align: center;
}

.bottom-bar{
	border: none !important;
	height: 52px !important;
}

.content-main{
	bottom: 52px !important;
}

.margin-toolbar-top{
	top: 44px !important;
}

.page--material.margin-toolbar-top{
	top: 56px !important;
}

.loading-spinner{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: white;
	z-index: 100;
}

.loading-spinner ons-progress-circular{
	width: 50px;
	height: 50px; 
}

.loading-spinner ons-progress-circular .progress-circular__primary{
	stroke: #0076ff !important;
}

.hidden{
	visibility: hidden;
}

.visable{
	visibility: visible;
}


input[type=range] {
pointer-events: none;
}

input[type=range]::-webkit-slider-thumb {
pointer-events:auto;
}

.accordian-contents>.accordian{
	background-image: linear-gradient(0deg, #f1f1f1, #fefefe 100%);
	padding-bottom: 0.5em;
}

.accordian-contents>.accordian>.accordian-contents{
	padding: 0px;
	margin: 0.5em 1em;
	border: 1px solid #e9e9e9;
}

.accordian>.list-item .center{
	display: none;
	z-index: -100;
}

ons-splitter-side{
	padding-right: 2px;
	border: 1px solid #e9e9e9;
}

ons-splitter-side .page .page__background{
	background-color: #fbfbfb !important;

}

.list-item--material__center {
    border-bottom: 1px solid #e9e9e9 !important;
}